@charset "UTF-8";

@import "base";

/*
    layout style
 */
.wrap{position: relative; margin: 0 auto; max-width: 2560px;}

.header{z-index: 10; display: flex; justify-content: space-between; align-items: center; position: absolute; left: 0; top: 0; padding: 30px 44px 0; width: 100%;}
.header h1{width: 137px;}
.header_util{text-align: right; color: #fff;}
.header_util .title{font-size: 24px; font-weight: 700; line-height: 1.4;}
.header_util .num{font-size: 40px; font-weight: 500;}

.footer{padding: 60px 0; background-color: #1c1c1c;}
.footer .inner{position: relative; margin: 0 auto; padding: 0 30px; max-width: 1260px; width: 100%;}
.footer .inner .link_list{display: flex; justify-content: center; align-items: center; position: absolute; right: 30px; top: 0;}
.footer .inner .link_list li{display: flex; align-items: center; justify-content: center; color: #fff; line-height: 1.6;}
.footer .inner .link_list li:first-of-type{color: #8338ec; font-weight: 500;}
.footer .inner .link_list li:first-of-type::after{content:""; display: block; margin: -2px 16px 0; width: 3px; height: 3px; -webkit-border-radius: 50%;-moz-border-radius: 50%;border-radius: 50%; background-color: #fff;}
.footer .inner .address{width: 70%;}
.footer .inner .address .con{display: flex; flex-wrap: wrap;}
.footer .inner .address .con p{margin-right: 20px; color: #a6a5a5; line-height: 1.6;}
.footer .inner .address .con p:last-of-type{margin-right: 0;}
.footer .inner .copy{margin-top: 20px; color: #a6a5a5;}
.footer .inner .copy span{font-weight: 700;}

.inquiry_frame{width: 100%; height: 82px;}
.inquiry_sec{z-index: 1000; position: fixed; left: 0; bottom: 0; width: 100%;}
.inquiry_sec.bot{position: relative;}
.inquiry{padding: 15px 0; width: 100%; background-color: #8338ec;}
.inquiry .inner{position: relative; display: flex; align-items: center; justify-content: center; margin: 0 auto; padding: 0 30px; max-width: 1260px; width: 100%;}
.inquiry .inner .tel{display: none;}
.inquiry .inner .title{display: flex; align-items: center; font-size: 36px; font-weight: 500; color: #fff;}
.inquiry .inner .title img{margin-right: 10px; width: 36px;}
.inquiry .inner .title p{margin-top: 2px;}
.inquiry .inner .frm{}
.inquiry .inner .frm .frm_inner{display: flex; align-items: center; justify-content: center; margin-left: 20px;}
.inquiry .inner .frm input[type='text']{margin-right: 10px; padding: 0 15px; width: 180px; height: 52px; background-color: #fff;}
.inquiry .inner .frm input[type='text']::placeholder{color: #666666;}
.inquiry .inner .frm input[type='checkbox']{display: none;}
.inquiry .inner .frm .agree_chk{display: block; position: relative; margin: 0 10px; padding-left: 40px; font-size: 14px; color: #fff; line-height: 1.5;}
.inquiry .inner .frm .agree_chk::before{content:""; position: absolute; left: 0; top: 50%; transform: translateY(-50%); width: 29px; height: 29px; background: url("../img/ico_check_off.png") no-repeat center center / 100% auto;}
.inquiry .inner .frm .agree_chk span{display: block; text-decoration: underline; text-underline-position : under;}
.inquiry .inner .frm #agree_chk:checked + .agree_chk::before{background-image: url("../img/ico_check_on.png");}
.inquiry .inner .frm button{margin-left: 20px;width: 180px; height: 52px; background-color: #1c1c1c; font-size: 18px; color: #fff;}

.swiper-wrapper{transition-timing-function: linear;}

@media all and (max-width:1280px){
    .inquiry .inner .title{font-size: 28px;}
    .inquiry .inner .title img{width: 30px;}
    .inquiry .inner .frm input[type=text]{width: 160px;}
    .inquiry .inner .frm button{width: 160px;}
}

@media all and (max-width:1140px){
    .inquiry .inner .title{font-size: 18px;}
    .inquiry .inner .title img{width: 24px;}
    .inquiry .inner .frm input[type=text]{width: 130px;}
    .inquiry .inner .frm button{width: 130px;}
}

@media all and (max-width:960px){
    .footer .inner{text-align: center;}
    .footer .inner .link_list{position: relative; right: 0; margin-bottom: 30px;}
    .footer .inner .address{width: 100%;}
    .footer .inner .address .con{justify-content: center;}

    .inquiry_frame{width: 100%; height: 90px;}
    .inquiry_sec{height: 90px;}
    .inquiry{position: absolute; left: 0; bottom: 0; width: 100%; padding: 0; background: none;}
    .inquiry .inner {display: block; padding: 0;}
    .inquiry .inner .mo_con{display: flex; justify-content: center; align-items: center; height: 90px;}
    .inquiry .inner .tel{display: block; width: 50%; height: 100%; background-color: #8338ec;}
    .inquiry .inner .tel a{display: flex; justify-content: center; align-items: center; position: relative; padding: 0 30px; width: 100%; height: 100%; font-size: 38px; font-weight: 500; color: #fff;}
    .inquiry .inner .tel a::before{content:""; display: block; margin-right: 15px; width: 50px; height: 50px; background: url("../img/ico_kakao.png") no-repeat center center / 100% auto;}
    .inquiry .inner .title{position: relative; padding: 0 30px; width: 50%; height: 100%; font-size: 34px; background-color: #5208bb; cursor: pointer;}
    .inquiry .inner .title::after{content:""; position: absolute; right: 30px; top: 50%; transform: translateY(-50%); width: 28px; height: 28px; background: url("../img/ico_plus.png") no-repeat center center / 100% auto;}
    .inquiry.active .inner .title::after{background-image: url("../img/ico_close.png");}
    .inquiry .inner .title img{width: 40px;}
    .inquiry .inner .frm{display: none; background: #5208bb;}
    .inquiry .inner .frm .frm_inner{display: block; margin: 0; padding: 50px 60px 90px;}
    .inquiry .inner .frm input[type='text']{margin-right: 0; margin-bottom: 20px; padding: 0 30px; width: 100%; height: 80px; font-size: 26px;}
    .inquiry .inner .frm .check_con{text-align: center;}
    .inquiry .inner .frm .agree_chk{display: inline-block; margin: 10px 0 30px; padding-left: 50px; font-size: 22px;}
    .inquiry .inner .frm .agree_chk::before{width: 30px; height: 30px;}
    .inquiry .inner .frm .agree_chk span{display: inline;}
    .inquiry .inner .frm button{margin-left: 0; width: 100%; height: 80px; font-size: 32px; font-weight: 700; background-color: #000000;}
}

@media all and (max-width:768px){
    .header{padding: 3.26vw 0 0 3.91vw;}
    .header h1{width: 19.14vw;}
    .header_util{display: none;}

    .footer .inner{padding: 0 3.91vw;}
    .footer .inner .link_list li{font-size: 3.39vw;}
    .footer .inner .address .con p{margin-right: 2.60vw; font-size: 3.39vw; line-height: 1.4;}
    .footer .inner .copy{margin-top: 5.21vw; font-size: 3.39vw;}

    .inquiry_frame{height: 11.72vw;}
    .inquiry_sec{height: 11.72vw;}
    .inquiry{padding: 0; background: none;}
    .inquiry .inner {padding: 0;}
    .inquiry .inner .mo_con{height: 11.72vw;}
    .inquiry .inner .tel{display: block; width: 50%; height: 100%; background-color: #8338ec;}
    .inquiry .inner .tel a{display: flex; justify-content: center; align-items: center; position: relative; padding: 0 3.91vw; width: 100%; height: 100%; font-size: 4.95vw; font-weight: 500; color: #fff;}
    .inquiry .inner .tel a::before{content:""; display: block; margin-right: 1.95vw; width: 6.51vw; height: 6.51vw; background: url("../img/ico_kakao.png") no-repeat center center / 100% auto;}
    .inquiry .inner .title{position: relative; padding: 0 3.91vw; width: 50%; height: 100%; font-size: 4.43vw; background-color: #5208bb; cursor: pointer;}
    .inquiry .inner .title::after{content:""; position: absolute; right: 3.91vw; top: 50%; transform: translateY(-50%); width: 3.65vw; height: 3.65vw; background: url("../img/ico_plus.png") no-repeat center center / 100% auto;}
    .inquiry .inner .title img{width: 5.21vw;}
    .inquiry .inner .frm .frm_inner{padding: 6.51vw 7.81vw 11.72vw;}
    .inquiry .inner .frm input[type='text']{margin-bottom: 2.60vw; padding: 0 3.91vw; height: 10.42vw; font-size: 3.39vw;}
    .inquiry .inner .frm .agree_chk{margin: 1.30vw 0 3.91vw; padding-left: 6.51vw; font-size: 2.86vw;}
    .inquiry .inner .frm .agree_chk::before{width: 3.91vw; height: 3.91vw;}
    .inquiry .inner .frm button{width: 100%; height: 10.42vw; font-size: 4.17vw;}
}

/*
    main visual
 */
.main_visual{overflow: hidden; position: relative; height: 920px; background: url("../img/main_visual.jpg") no-repeat center center / cover;}

.main_star{position: absolute; left: 0; top: 0; width: 100%; height: 100%;}
.main_star .stars{position: absolute; left: 50%; top: 0; transform: translateX(-50%); width: 100%; height: 100%; text-align: center;}
.main_star .stars1{animation: stars 1.1s 0.6s ease-in-out infinite;}
.main_star .stars2{animation: stars 1.6s 1.2s ease infinite;}
.main_star .stars3{animation: stars 2.3s 0.3s linear infinite;}
.main_star .stars4{animation: stars 1.3s 0.8s ease-out infinite;}
.main_star .stars img{display: block; margin: 0 auto; width: auto; height: 100%; object-fit: cover;}

.main_con{z-index: 20; position: relative;}

.main_title{padding-top: 200px;}
.main_title.animate{-webkit-animation-name: fadeInUp; animation-name: fadeInUp;}
.main_title .tag{margin: 0 auto; width: 453px;}
.main_title h2{margin: 20px 0 30px; font-size: 94px; font-weight: 500; color: #fff; text-align: center; line-height: 1.25;}
.main_title h2 span{display: inline-block; width: 525px; vertical-align: middle;}

.main_desc{text-align: center;}
.main_desc p{font-size: 32px; font-weight: 500; color: #fff;}
.main_desc p.point{color: #ff43ea;}
.main_desc p.point span{position: relative;}
.main_desc p.point span::before{content:""; position: absolute; left: 50%; top: -15px; transform: translateX(-50%); width: 6px; height: 6px; -webkit-border-radius: 50%;-moz-border-radius: 50%;border-radius: 50%; background-color: #ff43ea;}

.main_slide{overflow: hidden; position: absolute; bottom: 65px; width: 100%; background-color: #8438eb;}
.main_slide .swiper-slide{width: 607px;}

.main_product{z-index: 10; position: absolute; left: 0; bottom: 0; width: 100%;}
.main_product .items{position: absolute; bottom: -130px; width: 387px;}
.main_product .items:nth-child(2){right: 0;}

.nav{z-index: 9999; opacity: 0; position: fixed; left: 0; top: -80px; width: 100%; height: 80px; background-color: #0e0e0e; transition: all ease-in-out 0.3s; }
.nav_list{display: flex; justify-content: center; align-items: center; height: 100%;}
.nav_list li{margin: 0 23px; font-size: 22px; color: #fff;}
.nav_list li.active{color: #e814ff;}

.nav.scrollDown{ opacity: 1;  top: 0; }

@media all and (max-width:1600px){
    .main_product .items{bottom: -100px; width: 300px;}
    .main_product .items:nth-child(1){left: 5%;}
    .main_product .items:nth-child(2){right: 5%;}
}

@media all and (max-width:1600px){
    .main_product .items{bottom: -100px; width: 300px;}
}

@media all and (max-width:1400px){
    .main_title .tag{width: 350px;}
    .main_title h2{font-size: 75px;}
    .main_title h2 span{width: 430px;}
    .main_desc p{font-size: 28px;}
}

@media all and (max-width:1200px){
    .main_product .items:nth-child(1){left: 2%;}
    .main_product .items:nth-child(2){right: 2%;}
}

@media all and (max-width:960px){
    .nav_list li{margin: 0 16px; font-size: 18px;}
    .main_product .items:nth-child(1){left: 50%; transform: translateX(-50%);}
    .main_product .items:nth-child(2){display: none;}
}

@media all and (max-width:768px){
    .header{padding: 3.91vw 3.91vw 0;}
    .header h1{width: 18.62vw;}

    .main_visual{height: 165.76vw; background-image: url("../img/mo_main_visual.jpg");}

    .main_title{padding-top: 26.04vw;}
    .main_title .tag{width: 65.49vw;}
    .main_title h2{margin: 5.21vw 0 2.60vw; font-size: 11.07vw;}
    .main_title h2 span{width: 56.12vw;}

    .main_desc p{font-size: 4.43vw;}
    .main_desc p.point span::before{top: -1.95vw; width: 0.78vw; height: 0.78vw;}

    .main_slide{bottom: 10.42vw;}
    .main_slide .swiper-slide{width: 79.04vw;}

    .main_product .items{position: absolute; bottom: -32.55vw; width: 54.69vw;}
    .main_product .items1{left: 50%; transform: translateX(-50%);}
    .main_product .items1.animate{-webkit-animation-name: none; animation-name: none;}
    .main_product .items2{display: none;}

    .nav{height: 11.72vw;}
    .nav_list li{margin: 0 1.95vw; font-size: 3.13vw;}
}

/*
    content-common
 */
.title_box{z-index: 1; position: relative; text-align: center;}
.title_box .tag{margin-bottom: 20px; font-size: 28px; font-weight: 500; color: #1c1c1c;}
.title_box .title{margin: 0 auto; width: fit-content; font-family: 'SB Aggro', sans-serif; font-size: 70px; font-weight: 500; color: #1c1c1c;}
.title_box .title:last-child{margin-bottom: 50px;}
.title_box .title + .desc{margin-top: 30px;}
.title_box .desc{font-size: 22px; color: #1c1c1c; line-height: 1.6;}

.sub_title_box{text-align: center;}
.sub_title_box .sub_title{margin: 0 auto; width: fit-content; font-family: 'SB Aggro', sans-serif; font-size: 48px; font-weight: 500; color: #1c1c1c;}
.sub_title_box .sub_title:last-child{margin-bottom: 67px;}
.sub_title_box .sub_title + .desc{margin-top: 26px;}
.sub_title_box .desc{font-size: 22px; color: #565656; line-height: 1.6;}

.highlight{position: relative; margin-left: auto; margin-right: auto; width: fit-content; font-size: 78px; color: #fff;}
.highlight span{position: relative; z-index: 1;}
.highlight::before{content:""; position: absolute; left: -5px; top: 45%; transform: translateY(-50%); width: 0; height: calc(100% + 5px); background-color: #e814ff; transition: width 0.5s;}
.highlight.bg_black::before{background-color: #000;}
.highlight.bg_purple::before{background-color: #9f16ff;}
.highlight.animate::before{width: calc(100% + 10px);}

.notice_list{font-size: 22px; color: #565656;}
.notice_list li{display: flex; align-items: center;}
.notice_list li:before{content: ''; display: inline-block; margin-right: 16px; width: 5px; height: 5px; background: #7d32e6;}
.notice_list li + li{margin-top: 10px;}

.swiper-button-con{position: absolute; left: 0; top: 50%; transform: translateY(-50%); width: 100%;}
.swiper-button-next, .swiper-button-prev{margin-top: 0; width: 23px; height: 23px;}
.swiper-button-next{right: 0; background: url("../img/ico_arrow_right.png") no-repeat center center / 100% auto;}
.swiper-button-prev{left: 0; background: url("../img/ico_arrow_left.png") no-repeat center center / 100% auto;}

.tab_img li{display: none;}
.tab_img li.active{display: block;}
.tab_cont{display: none;}
.tab_cont.active{display: block;}
.more_cont{display: none; margin-top: 30px;}

@media all and (max-width:1200px){
    .title_box .tag{font-size: 22px;}
    .title_box .title{font-size: 60px;}
    .highlight{font-size: 66px;}
}

@media all and (max-width:768px){
    .title_box .tag{margin-bottom: 3.91vw; font-size: 4.43vw;}
    .title_box .title{font-size: 10.16vw; letter-spacing: -0.3rem;}
    .title_box .title:last-child{margin-bottom: 9.11vw;}
    .title_box .title + .desc{margin-top: 5.21vw;}
    .title_box .desc{padding: 0 12.24vw; font-size: 3.91vw; word-break: keep-all;}
    .title_box .desc br{display: none;}

    .sub_title_box .sub_title{font-size: 6.51vw;}
    .sub_title_box .sub_title:last-child{margin-bottom: 9.11vw;}
    .sub_title_box .sub_title + .desc{margin-top: 4.69vw;}
    .sub_title_box .desc{font-size: 3.65vw;}

    .highlight{font-size: 10.16vw;}
    .highlight::before{height: calc(100% + 0.65vw);}
    .highlight.animate::before{width: calc(100% + 1.3vw);}

    .notice_list{font-size: 3.65vw;}
    .notice_list li:before{margin-right: 1.30vw; width: 0.65vw; height: 0.65vw;}
    .notice_list li + li{margin-top: 1.56vw;}
}

/*
    section01
 */
.section01{padding-top: 200px; background: url("../img/section_01_bg.jpg") no-repeat center center / cover;}
.section01 .info .title_box .title .compare{display: flex; justify-content: center; align-items: center;}
.section01 .info .title_box .title .compare .highlight{margin-left: 15px; color: #fff;}

.section01 .info .flex_con{display: flex; justify-content: center; align-items: center; margin-bottom: 100px; padding: 0 40px;}
.section01 .info .flex_con.reverse{flex-direction: row-reverse;}
.section01 .info .flex_con .img_con{overflow: hidden; position: relative; width: 700px; border: 5px solid transparent; background-image: linear-gradient(#fff, #fff), linear-gradient(149deg, rgba(18,140,255,1) 0%, rgba(148,72,255,1) 50%, rgba(255,19,152,1) 100%); border-image-slice: 1; background-origin: border-box; background-clip: content-box, border-box; border-radius: 0 50px 0 50px;}
.section01 .info .flex_con .txt_con{padding: 0 100px;}
.section01 .info .flex_con .txt_con dt{margin-bottom: 25px; font-size: 48px; font-weight: 500; color: #fff;}
.section01 .info .flex_con .txt_con dt span{display: block; color: #e814ff;}
.section01 .info .flex_con .txt_con dd{font-size: 22px; color: #ddd; line-height: 1.5;}
.section01 .info .flex_con .txt_con dd + dd{margin-top: 20px;}
.section01 .info .flex_con .txt_con dd.line{font-weight: 500; text-decoration: underline; text-underline-position : under; }
.section01 .info .flex_con .txt_con dd span{display: block; color: #e814ff;}

.section01 .info .flex_con .icon_con{display: flex; align-items: flex-end; justify-content: center; position: absolute; left: 50%; top: 50%; transform: translate(-50%, -50%);}
.section01 .info .flex_con .icon_con p{text-align: center;}
.section01 .info .flex_con .icon_con p span{display: block; margin-top: 10px; font-size: 30px; font-weight: 700; color: #a1a1a1; white-space: nowrap;}
.section01 .info .flex_con .icon_con p.blod{margin-left: 170px;}
.section01 .info .flex_con .icon_con p.blod span{font-size: 40px; color: #f5259e;}
.section01 .info .flex_con .icon_con p.icon{position: absolute; left: 47%; top: 50%; transform: translate(-50%, -50%);}


.section01 .banner{display: flex; align-items: center; justify-content: center; flex-direction: column; margin: 0 auto; width: calc(100% - 120px); height: 460px; text-align: center; background: url("../img/section_01_banner.jpg") no-repeat center center / cover; border: 5px solid transparent; border-image: linear-gradient(149deg, rgba(18,140,255,1) 0%, rgba(148,72,255,1) 50%, rgba(255,19,152,1) 100%); border-image-slice: 1;}
.section01 .banner .title{margin-bottom: 20px; width: 100%; font-size: 48px; font-weight: 500; color: #fff;}
.section01 .banner .title .highlight{margin: 0 auto; width: fit-content;}
.section01 .banner .title br{display: none;}
.section01 .banner .txt{font-size: 22px; color: #fff; line-height: 1.5;}

